@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,600;1,300&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
  height: 100%;
}

.nav {
  background-color: #333;
  color: white;
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  width: 100%;
}

.nav h1 {
  margin-left: 60px;
}

.icon {
  margin-right: 120px;
  display: block;
  position: relative;
}

.amount-container {
  position: absolute;
  top: -0.85rem;
  right: -0.85rem;
  width: 1.75rem;
  height: 1.75rem;
  border-radius: 50%;
  background: grey;
  display: flex;
  align-items: center;
  justify-content: center;
}
.total-amount {
  color: white;
  margin-bottom: 0;
  font-size: 1.25rem;
}

.container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  height: 100vh;
  justify-items: center;
  justify-content: space-evenly;
  margin-bottom: 340px;
}

.wrapper {
  text-align: center;
  margin-top: 70px;
}

.card {
  display: flex;
  flex-direction: column;
  width: 50%;
  padding: 10px;
  background-color: white;
  margin-top: 35px;
  margin-left: 90px;
}

.item-detail {
  border: none;
  padding: 5px;
  margin: 25px 0;
  border-radius: 5px;
  background-color: black;
  color: white;
  text-decoration: none;
}

.item-detail:hover {
  text-transform: uppercase;
}

.link {
  color: white;
  text-decoration: none;
}

.details {
  text-align: center;
}

.details h4 {
  margin: 5px 0;
}

.details h2 {
  margin: 8px 0;
}

.details h3 {
  margin: 6px 0;
  font-style: italic;
}

.footer {
  height: 70px;
  background-color: #333;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  width: 100%;
}

.cart-container {
  text-align: center;
  min-height: 100vh;
}

.cart-container h1 {
  margin: 30px 0;
}

.cart-wrapper {
  display: flex;
  flex-direction: column;
  margin-top: 55px;
  margin-bottom: 55px;
}

.cart-item {
  display: flex;
  justify-content: space-evenly;
  margin: 20px 0;
  align-items: center;
}

.home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
}

.home h1 {
  margin: 20px 0;
}

.home h3 {
  margin: 40px 0;
}

.home img {
  width: 70%;
  height: 500px;
}

.btn {
  display: inline-block;
  width: 200px;
  height: 100px;
  border: none;
  color: white;
  background: black;
  margin: 25px 0;
  font-size: 2rem;
}

.specific-item {
  display: flex;
  width: 80%;
  margin-top: 30px;
  justify-content: space-between;
  margin-bottom: 30px;
}

.specific-item h1 {
  margin: 20px 0;
}

.written {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.controls {
  display: flex;

  align-items: center;
  justify-content: space-evenly;
}

.controls h1 {
  font-size: 3rem;
}

.quant {
  background-color: #d3d3d3;
}

.quant:hover {
  background-color: grey;
}

.add-cart {
  display: block;
  padding: 8px;
  border: none;
  background-color: black;
  color: white;
  margin: 30px 0;
}

.add-cart:hover {
  background-color: grey;
}

.qty {
  display: flex;
  align-items: center;
}

.arrows {
  display: flex;
  flex-direction: column;
}

.clicker {
  color: #43464b;
}

.clicker:hover {
  color: blue;
}

.total-container {
  display: flex;
  justify-content: flex-end;
  margin: 45px 160px;
}

.total-container h1 {
  margin: 0 12px;
}

.test {
  min-height: 100vh;
  background-image: url(/shopping-cart/static/media/hero.c1018e10.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.test-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 100%;
  padding: 0 20px;
  color: white;
  position: absolute;
  z-index: 9;
  width: 100%;
  line-height: 1.6;
}

.test-content h2 {
  margin: 20px 0;
}

.test::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}

